        title: `LITSEY YUTUQLARI`,
        content: `Toshkent kimyo-texnologiya litseyi kopyillik tarixi mobaynida minglab oquvchilarga talim berish bilan birga, Ozbekiston kelajagi uchun zarur bolgan har tomonlama bilimdon mutaxassis-kadrlarni tayyorlab kelmoqda. Akademik litsey oquvchilari orasida salohiyatli va bilimli oquvchilar talaygina. 
Otgan oquv yillari mobaynida oquvchilar umumtalim fanlari, sport va madaniyat sohalarida etirof etishli arzigulik yutuqlarga erishdilar. Bunga, albatta, ustozlarning mashaqqatli mehnati va oquvchilarning intilishlari sabab boldi. Jumladan: 
2021/2022-oquv yilining 3-, 4-noyabr kunlari Toshkent shahri Yunusobod tumani TDShU akademik litseyida Basketbol sport turi boyicha otkazilgan musobaqada litsey jamoasi 3-orinni (qiz, ogil) qolga kiritdi.
2022-yil 9-, 10-noyabr kunlari Toshkent shahri Yunusobod tumani TPTI akademik litseyida Voleybol sport turi boyicha otkazilgan musobaqada litsey jamoasi 2-orinni (qiz, ogil)  qolga kiritdi.
`,
